import {Component} from '@angular/core';
declare const google:any,window:any;


@Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.css']
})
export class AppComponent {

    title = 'ImLive';
    Notification: any;
    userLocation = {
        "lat": 0.0,
        "lng": 0.0
    }

    ngOnInit() {
        localStorage.setItem("userLoc", JSON.stringify(this.userLocation))
        this.getLocation();
    }

    getLocation() {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(this.setUserLocation, this.showError);
        } else {
            this.Notification = "Geolocation is not supported by this browser.";
        }
    }

    showError = (error) => {
        switch (error.code) {
            case error.PERMISSION_DENIED:
                this.Notification = "Error : Share Location"
                break;
            case error.POSITION_UNAVAILABLE:
                this.Notification = "Error : Location unavailable."
                break;
            case error.TIMEOUT:
                this.Notification = "The request to get user location timed out."
                break;
            case error.UNKNOWN_ERROR:
                this.Notification = "Error : An unknown error occurred."
                break;
        }
    }

    setUserLocation = (position) => {
        this.userLocation.lat = position.coords.latitude;
        this.userLocation.lng = position.coords.longitude;
        localStorage.setItem("userLoc", JSON.stringify(this.userLocation))
    }

}



